import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Service } from '../../../providers/service/service';
import { Values } from '../../../providers/service/values';
//import { OrderDetails } from '../order-details/order-details';
import { Functions } from '../../../providers/service/functions';

@IonicPage()
@Component({
  templateUrl: 'return-product.html'
})
export class ReturnProduct {

  id: any;
  form: any;
  href:any;
  results: any;
  buttonText: any;
  public disableSubmit: boolean = false;

  constructor(public nav: NavController, public service: Service, params: NavParams, public functions: Functions, public values: Values) {

    this.href = params.data;
    this.buttonText = this.values.lan.text_button_continue;  

  	this.service.getForm(this.href)
    .then((results) => this.form = results);  
  }

 
  saveReturnProduct(href){
    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText=this.values.lan.text_loading; 

      this.service.saveReturnProduct(this.form, href)
      .then((results) => this.handleResults(results));
    }
  }
 
  
  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.values.lan.text_button_continue;
    this.functions.showAlert(this.values.lan.text_success, results.text_message);
    this.nav.pop();
  }

  
  encodeUrl(href){
    return href.replace(/&amp;/g, '&');
  }

  
  validateForm(){
    
    if(this.form.return_reason_id == undefined || this.form.return_reason_id == ""){this.functions.showAlert(this.values.lan.text_error, this.values.lan.text_error_return_reason); return false}
    if(this.form.opened == undefined || this.form.opened == false){this.functions.showAlert(this.values.lan.text_error, this.values.lan.text_error_product_opened); return false}
    if(this.form.firstname == undefined || this.form.firstname == ""){return false}
    if(this.form.email== undefined || this.form.email == ""){return false}
    if(this.form.order_id == undefined || this.form.order_id == ""){return false}
    if(this.form.product == undefined || this.form.product == ""){return false}
    if(this.form.quantity == undefined || this.form.quantity == ""){return false}
    if(this.form.model == undefined || this.form.model == ""){return false}
    
    return true
  }

}


